import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, UnidadeMedida } from '../../../../models/dtos/ger/unidadeMedida';

interface UnidadeMedidaEditViewProps extends EditViewProps<UnidadeMedida> {}

const UnidadeMedidaEditView: FC<UnidadeMedidaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<UnidadeMedida>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 115;

  const validateExtras = (data: UnidadeMedida): string[] => {
    const errors = [];

    if (data.abreviacao && data.descricao) {
      if (data.abreviacao.toUpperCase() === data.descricao.toUpperCase()) {
        errors.push('A descrição deve ser diferente da abreviação.');
      }
    }

    return errors;
  };

  return (
    <WindowComponent title="Unidade de Medida">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 6, required: true } }}
              id="abreviacao"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              label="Abreviação"
              labelWidth={defaultWidth}
              placeholder="Informe a Abreviação da Unidade de Medida"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 60, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição da Unidade de Medida"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default UnidadeMedidaEditView;
